import React, { Fragment } from 'react';
import classes from './Footer.module.css';
import Nav from 'react-bootstrap/Nav';
import { Link, withRouter } from "react-router-dom";

const Footer = (props) => {
    return (
        <Fragment>
            <footer className={classes.Footer}>
                <Nav className="justify-content-center">
                    <Link to={{ pathname: props.match.url + 'about-us' }} className='text-white'>About-Us</Link>
                    <Link to={{ pathname: props.match.url + 'contact-us' }} className='text-white ml-4'>Contact-Us</Link>
                    <Link to={{ pathname: props.match.url + 'customer-service' }} className='text-white ml-4'>Customer-Service</Link>
                </Nav>
                <div className="text-center text-muted">
                    <small >Copyright &copy; {new Date().getFullYear()}. All Rights Reserved</small>
                </div>
            </footer>
        </Fragment>
    );
};

export default withRouter(Footer);