import React, { Fragment } from 'react';
import classes from './Header.module.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link, withRouter } from "react-router-dom";

const Header = (props) => {
    return (
        <Fragment>
            <header className={classes.Header}>
                <Navbar collapseOnSelect expand="lg" variant="dark" sticky="top">
                    <Navbar.Brand>
                        <Link to={{ pathname: props.match.url }} className='mr-3 text-decoration-none'>Routing App</Link>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto">
                            <Link to={{ pathname: props.match.url, }}
                                className='mr-3'>Home</Link>
                            <Link to={{ pathname: props.match.url + 'users' }} className='mr-3'>Users</Link>
                            <Link to={{ pathname: props.match.url + 'features' }} className='mr-3'>Features</Link>
                            <Link to={{ pathname: props.match.url + 'pricing' }} className='mr-3'>Pricing</Link>
                            <Link to={{ pathname: props.match.url + 'feedback' }} className='mr-3'>Feedback</Link>
                        </Nav>
                        <Nav>
                            <Link to={{ pathname: props.match.url + 'login' }} className='mr-3'>LogIn</Link>
                            <Link to={{ pathname: props.match.url + 'register' }} className='mr-3'>Register</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </header>
        </Fragment>
    );
};

export default withRouter(Header);