import React, { Fragment } from 'react';
import Classes from './ChildNavs.module.css'
import { Link, Switch, Route, useRouteMatch } from 'react-router-dom';

const ChildNavs = (props) => {
    const { path, url } = useRouteMatch();

    return (
        <Fragment>
            <div className={Classes.ChildNavs}>
                <h3>Features</h3>
                <Link to={`${url}/flexiblity`}>Flexiblity</Link><br />
                <Link to={`${url}/durability`}>Durability</Link><br />
                <Link to={`${url}/time-saving`}>Time-Saving</Link>
                <Switch>
                    <div className='mt-3'>
                        <Route path={`${path}/flexiblity`}>
                            <h5 className='mb-4'>Flexiblity</h5>
                        </Route>
                        <Route path={`${path}/durability`}>
                            <h5 className='mb-4'>Durability</h5>
                        </Route>
                        <Route path={`${path}/time-saving`}>
                            <h5 className='mb-4'>Time-Saving</h5>
                        </Route>
                    </div>
                </Switch>
            </div>
        </Fragment>
    );
};

export default ChildNavs;